import classNames from "classnames";
import React from "react";
import { Link } from "react-router-dom";

interface INavbarDropdownMenuItemProps {
  menuId: string;
  url: string;
  name: string;
  icon: string;
  childItems?: Array<{
    url: string;
    name: string;
  }>;
  activeMenu: string | null;
  setActiveMenu: (menuId: string | null) => void;
}

const NavbarDropdownMenuItem: React.FC<INavbarDropdownMenuItemProps> = ({
  menuId,
  url,
  name,
  icon,
  childItems,
  activeMenu,
  setActiveMenu,
}) => {
  const isActive = activeMenu === menuId;

  const toggle = () => {
    setActiveMenu(isActive ? null : menuId);
  };

  return (
    <li className="nav-item dropdown">
      <Link
        className="nav-link arrow-none"
        to={url}
        onClick={(e: React.ChangeEvent) => {
          if (childItems) {
            e.preventDefault();
            toggle();
          }
        }}
      >
        <i className={`${icon} me-2`}></i>
        {name} {isActive}
      </Link>
      {childItems && (
        <div
          className={classNames("dropdown-menu", {
            show: isActive,
          })}
        >
          {childItems.map(({ url, name }, index) => (
            <Link key={"" + index} to={url} className="dropdown-item">
              {name}
            </Link>
          ))}
        </div>
      )}
    </li>
  );
};

export default NavbarDropdownMenuItem;
