export function convertCsv(numbers: string[]): Blob {
  const header = "number\r\n";
  const csvContent = numbers.map(num => `\"${num}\"`).join("\r\n");
  const csvString = header + csvContent;
  const csvBlob = new Blob([csvString], { type: "text/csv;charset=utf-8" });
  return csvBlob;
}

export function triggerDownloadCsv(csvFile: Blob): void {
  const timestamp = new Date().getTime();
  const filename = `numbers-${timestamp}.csv`;
  const csvUrl = URL.createObjectURL(csvFile);

  const link = document.createElement("a");
  link.href = csvUrl;
  link.download = filename;
  document.body.appendChild(link);
  link.click();
  document.body.removeChild(link);
  URL.revokeObjectURL(csvUrl);
}
