import React from "react";
import {
  Button,
  Card,
  CardBody,
  CardSubtitle,
  CardTitle,
  Col,
  Container,
  Row,
} from "reactstrap";
import styled from "styled-components";
import useNumberGenerator from "hooks/useNumberGenerator";
import { convertCsv, triggerDownloadCsv } from "hooks/useCsvConverter";

const CHOICES = [50, 100, 500, 1000, 2500, 5000, 7500, 10000];

const ButtonsContainer = styled.div`
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  gap: 5px;
`;

const CustomButton = styled(Button)`
  width: 100px;
`;

const NumberImport: React.FC = () => {
  document.title = "ランダム番号生成 | ガッツリ番号生成くん";
  const numberGenerator = useNumberGenerator();

  return (
    <React.Fragment>
      <div className="page-content">
        <Container fluid>
          <Row>
            <Col xs={12}>
              <Card>
                <CardBody className="pb-5">
                  <CardTitle className="mb-3">ランダム番号生成</CardTitle>
                  <CardSubtitle className="mb-4">
                    ランダムに生成したい電話番号の件数を選択してください。件数が多い場合、処理に時間がかかることがございます。
                  </CardSubtitle>
                  <ButtonsContainer>
                    {CHOICES.map(choice => (
                      <CustomButton
                        key={choice}
                        color="success"
                        onClick={() => {
                          const numbers = numberGenerator(10);
                          const csvFile = convertCsv(numbers);
                          triggerDownloadCsv(csvFile);
                        }}
                      >
                        {choice}件
                      </CustomButton>
                    ))}
                  </ButtonsContainer>
                </CardBody>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    </React.Fragment>
  );
};

export default NumberImport;
