const PREFIX: string[] = ["090"]; // Add 080 and 070 in the future

function generateRandomNumber(prefix: string[] = PREFIX): string {
  // Generate random phone number
  const numberChoices: string[] = [
    "123456789",
    "0123456789",
    "0123456789",
    "0123456789",
    "123456789",
    "0123456789",
    "0123456789",
    "0123456789",
  ];

  const randomChoice = (choices: string[]): string => {
    const index = Math.floor(Math.random() * choices.length);
    return choices[index];
  };

  const randomNumber: string = numberChoices
    .map(nc => randomChoice(nc.split("")))
    .join("");

  return `${randomChoice(prefix)}${randomNumber}`;
}

function useNumberGenerator(): (count: number) => string[] {
  return (count: number): string[] => {
    const numbers: string[] = [];
    for (let i = 0; i < count; i++) {
      numbers.push(generateRandomNumber());
    }
    return numbers;
  };
}

export default useNumberGenerator;
